import { __decorate } from "tslib";
import Vue from 'vue';
import Card from '@/components/Card.vue';
import ColorDot from '@/components/ColorDot.vue';
import { Component } from "vue-property-decorator";
import { GeneratePhoto } from '@/api/photo';
import { Getter, Mutation } from 'vuex-class';
import { Colors } from '@/model/Colors';
let PhotoIndex = class PhotoIndex extends Vue {
    form = {
        height: 0,
        width: 0,
        human_matting_model: "hivision_modnet",
        face_detect_model: "mtcnn"
    };
    colors = Colors.slice(0, 5);
    //本地文件地址
    imagePath = '';
    //图片预览遮罩
    show = false;
    //加载动画
    loading = false;
    //获取图片配置
    photoSize;
    //设置图片base64地址
    setPhotoPath;
    mounted() {
    }
    /**
     * 选择图片
     */
    chooseImage(type) {













        uni.chooseMedia({
            count: 1,
            mediaType: ['image'],
            sourceType: [type],
            success: (res) => {
                const path = res.tempFiles[0].tempFilePath;
                this.imagePath = path;
                this.show = true;
            }
        });

    }
    /**
     * 提交表单
     */
    submit() {
    }
    /**
     * 确认选择
     */
    async confirmSelect() {
        this.loading = true;
        const prefix = 'data:image/png;base64,';
        let transparentBase64 = '';
        let transparentBase64HD = '';
        let transparentBase64Path = '';
        let transparentBase64HDPath = '';
        //使用vuex中的尺寸
        this.form.height = this.photoSize.pxHeight;
        this.form.width = this.photoSize.pxWidth;
        //提交后台服务器  获取透明base64
        await GeneratePhoto(this.form, 'input_image', this.imagePath)
            .then(data => {
            if (!data) {
                throw new Error('API返回数据为空');
            }
            // 处理API响应
            console.log('API返回数据:', data);
            console.log('数据类型:', typeof data);
            console.log('数据键名:', Object.keys(data));
            // 检查API状态 - 根据HivisionIDPhotos API文档
            if (data.status !== 'success' && data.status !== true) {
                throw new Error('API处理失败: ' + (data.message || '未知错误'));
            }
            // 根据HivisionIDPhotos API文档，直接获取返回的字段
            transparentBase64 = data.image_base64_standard;
            transparentBase64HD = data.image_base64_hd;
            console.log('标准证件照base64:', transparentBase64 ? '已获取' : '未获取');
            console.log('高清证件照base64:', transparentBase64HD ? '已获取' : '未获取');
            if (!transparentBase64) {
                throw new Error('API返回的标准证件照数据不完整');
            }
            const fs = uni.getFileSystemManager();

            transparentBase64Path = `${wx.env.USER_DATA_PATH}/temp_image.png`;
            transparentBase64HDPath = `${wx.env.USER_DATA_PATH}/temp_image_hd.png`;

            //保存标清照
            fs.writeFile({
                filePath: transparentBase64Path,
                data: transparentBase64.replace(prefix, ''),
                encoding: 'base64',
                success(res) {
                    console.log(res, "写入本地成功");
                }
            });
            //保存高清照
            fs.writeFile({
                filePath: transparentBase64HDPath,
                data: transparentBase64HD.replace(prefix, ''),
                encoding: 'base64',
                success(res) {
                    console.log(res, "写入本地成功");
                }
            });
        })
            .catch(err => {
            console.error('生成证件照失败:', err);
            this.loading = false;
            uni.showModal({
                title: '处理失败',
                content: '图片处理失败，请检查网络连接或稍后重试',
                showCancel: false
            });
            return;
        });
        // 只有在成功获取数据后才继续
        if (!transparentBase64 || !transparentBase64HD) {
            this.loading = false;
            return;
        }
        //把处理后的base64存到本地
        const photoPath = {
            base64: transparentBase64,
            base64HD: transparentBase64HD,
            base64Path: transparentBase64Path,
            base64HDPath: transparentBase64HDPath
        };
        //保存到本地
        // uni.setStorageSync('photo:transparent', Photo)
        this.setPhotoPath(photoPath);
        uni.navigateTo({
            url: '/pages/photo/edit',
            success: () => {
                this.loading = false;
            }
        });
    }
    skipTo() {
        uni.navigateTo({
            url: '/pages/photo/index'
        });
    }
};
__decorate([
    Getter('photoConfig')
], PhotoIndex.prototype, "photoSize", void 0);
__decorate([
    Mutation('SET_PHOTO_PATH')
], PhotoIndex.prototype, "setPhotoPath", void 0);
PhotoIndex = __decorate([
    Component({ components: { Card, ColorDot } })
], PhotoIndex);
export default PhotoIndex;
